/*========================================================================*\
    > custom
\*========================================================================*/
.banner_wrap { position: relative; overflow: hidden; }

.banner_wrap .pic { width: 100%; height: 0; padding-bottom: 31.25%; background-size: cover; }

.banner_wrap .slogan1 { position: absolute; top: 30.66667%; left: 61.19792%; font-size: 28px; font-size: 1.75rem; color: #000; font-weight: bolder; opacity: 0; transition: all 0.3s cubic-bezier(0.12, 0.49, 0.17, 0.87); }

.banner_wrap .slogan2 { position: absolute; top: 42.66667%; left: 61.19792%; font-size: 62px; font-size: 3.875rem; color: #000; font-weight: bolder; opacity: 0; transition: all 0.3s cubic-bezier(0.12, 0.49, 0.17, 0.87); }

.banner_wrap .slogan3 { position: absolute; top: 61.33333%; left: 61.19792%; font-size: 16px; font-size: 1rem; color: #000; opacity: 0; transition: all 0.3s cubic-bezier(0.12, 0.49, 0.17, 0.87); }

.page_footer { border-top: 1px solid #aacf52; padding-top: 40px; }

.page_footer .copyright { padding-top: 10px; padding-bottom: 10px; background-color: #aacf52; text-align: center; color: #fff; font-size: 14px; font-size: 0.875rem; }

.footer_nav-item a { color: #000; }

.header_nav { position: absolute; top: 0; right: 1rem; height: 100%; }

.header_nav ul { height: 100%; }

.header_nav-item { position: relative; float: left; width: 120px; height: 100px; }

.header_nav-item .inner { display: flex; align-items: center; justify-content: center; width: 100%; height: 100%; text-align: center; color: #000; }

.header_nav-item:after { content: ''; display: block; position: absolute; bottom: 0; width: 100%; height: 0px; background-color: #aacf52; transition: all 0.3s cubic-bezier(0.12, 0.49, 0.17, 0.87); }

.header_nav-item:hover:after, .header_nav-item.active:after { height: 3px; }

.service_wrap { padding-top: 160px; }

.service_block { display: table; margin: auto; }

.service_block ul { display: flex; }

.service_item { width: 170px; opacity: 0; }

.service_item:not(:first-of-type) { margin-left: 130px; }

.service_item .title { margin-bottom: 1.2rem; font-size: 22px; font-size: 1.375rem; font-weight: bolder; line-height: 32px; }

.service_item .txt { line-height: 28px; }

.info_wrap { padding-top: 160px; }

.case_wrap { padding-top: 160px; padding-bottom: 160px; }
